import {Component, OnInit, Input, Output, EventEmitter, OnChanges , SimpleChanges} from '@angular/core';

@Component({
  selector: 'app-stars',
  templateUrl: './stars.component.html',
  styleUrls: ['./stars.component.scss']
})
export class StarsComponent implements OnInit,OnChanges {
  private stars:boolean[];//1.定义stars为布尔类型的数组 用来显示隐藏

  @Input() private rating:number = 0 ;//2.接收product组件传过来的星级评分

  @Output()
  private  ratingChange:EventEmitter<number> =  new EventEmitter();

  @Input() private readonly :boolean = true;//点击评星 读写开关

  constructor() { }

  ngOnInit() {
    // this.stars = [false,false,true,true,true];//3.数组赋值
  }
  ngOnChanges(changes: SimpleChanges): void {
    this.stars = [];
    for(let i=1;i<=5;i++){
      this.stars.push(i>this.rating);
    }
  }

  clickStar(index:number){
    if(!this.readonly){//点击star评分
      this.rating= index + 1;
      this.ratingChange.emit(this.rating);
    }
  }

}
