import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {ProductService,Product,Comment} from "../shared/product.service";
import {WebSocketService} from "../shared/web-socket.service";
import {Subscription} from "rxjs";


@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.component.html',
  styleUrls: ['./product-detail.component.scss']
})
export class ProductDetailComponent implements OnInit{
  product:Product;

  comments:Comment[];

  newRating:number = 5;//最新的评价星级
  newComment:string = "";//最新的评价内容

  isCommentHidden = true;//控制评论是否隐藏

  isWatched:boolean = false;//控制关注按钮显示隐藏
  currentBid:number;//当前出价

  subscrrpiton : Subscription;//订阅属性

  constructor(private routeInfo:ActivatedRoute ,
              private productService:ProductService,
              private wsService: WebSocketService) { }

  ngOnInit() {
    let productId:number = this.routeInfo.snapshot.params["productId"]

    this.productService.getProduct(productId).subscribe(//手工订阅 把值转换成流Observe<>
      product => {
        this.product = product
        console.log(product.price)
        this.currentBid = product.price;
      }
    )
    // this.comments = this.productService.getCommentsForProductId(productId)
    this.productService.getCommentsForProductId(productId).subscribe(//手工订阅 把值转换成流Observe<>
      comments => this.comments = comments
    )
  }

  addComment(){
    let comment = new Comment(0,this.product.id , new Date().toISOString(),"someone",this.newRating,this.newComment);
    this.comments.unshift(comment);

    let sum = this.comments.reduce((sum,comment) => sum+comment.rating , 0)
    this.product.rating = sum/this.comments.length;

    this.newComment = null;
    this.newRating = 5;
    this.isCommentHidden = true;
  }

  watchedProduct(){
    if(this.subscrrpiton){
      this.subscrrpiton.unsubscribe();
      this.isWatched = false;
      this.subscrrpiton = null
    }else {
      this.isWatched = true;
      this.subscrrpiton = this.wsService.createObservableSocket("ws://localhost:8085",this.product.id)
        .subscribe(
          products=>{
            let product = products.find( p=> p.productId == this.product.id);
            this.currentBid = product.bid
          }
        );
    }
  }
}
