import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { StarsComponent } from './components/stars/stars.component';
import { SearchComponent } from './components/search/search.component';
import { CarouselComponent } from './components/carousel/carousel.component';
import { FooterComponent } from './components/footer/footer.component';
import { ProductComponent } from './components/product/product.component';
import { ProductDetailComponent } from './components/product-detail/product-detail.component';
import { HomeComponent } from './components/home/home.component';
import {RouterModule, Routes} from "@angular/router";
import {ProductService} from "./components/shared/product.service";
import {WebSocketService} from "./components/shared/web-socket.service";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import { FilterPipe } from './components/pipe/filter.pipe';
import {HttpClientModule} from "@angular/common/http";
import {HashLocationStrategy, LocationStrategy} from "@angular/common";

const routeConfig:Routes = [
  {path:'',component:HomeComponent},
  {path:'product/:productId',component:ProductDetailComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    StarsComponent,
    SearchComponent,
    CarouselComponent,
    FooterComponent,
    ProductComponent,
    ProductDetailComponent,
    HomeComponent,
    FilterPipe,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    //主模块的使用forRoot来注入路由配置 子用forChild
    RouterModule.forRoot(routeConfig),
    //表格监控组件
    ReactiveFormsModule,
    FormsModule,
    HttpClientModule,
  ],
  providers: [ProductService,WebSocketService,{provide:LocationStrategy,useClass:HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }
